article {
    width: 100%;
    position: relative;
    &::after {
        content: '';
        display: block;
        clear: both;
    }
    &:nth-of-type(2n) {
        .pic {
            float: right;
        }
        .con {
            float: left;
            width: 600px;
            top: 150px;
            left: 6%;
        }
    }
    .pic {
        float: left;
        width: 50%;
        height: 400px;
        background: #fef9f3;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        
    }
    .con {
        float: right;
        position: absolute;
        top: 150px;
        left: 52%;
        h2 {
            font-size: 24px;
            color: #1f464a;
            margin-bottom: 16px;
        }
        p {
            font-size: 18px;
            color: #1f464a;
            margin-bottom: 12px;
        }
        span {
            font-size: 16px;
            color: #1f464a;
        }
        
    }
}
